@use '../utils/mixins' as *;

.footer {
  height: 180px;

  @include bg('../images/mobile/footer-bg-mob.png', cover);

  @include on-tablet {
    height: 359px;
    background-image: url('../images/tablet/footer-bg-tab.png');
  }

  @include on-desktop {
    background-image: url('../images/desktop/footer-bg-desk.png');
  }
}
